import "./App.css";
import "@ui5/webcomponents-icons/dist/action.js";
import "@ui5/webcomponents-icons/dist/download.js";
import imageToAdd from "./assets/images/minhafoto.jpg";
import geovaneImg from "./assets/images/geovane.jpg";
import diegoImg from "./assets/images/diego.jpg";

import React from "react";

import {
  ObjectPage,
  Avatar,
  Bar,
  Button,
  FlexBox,
  Link,
  Toolbar,
  Label,
  Breadcrumbs,
  MessageStrip,
  ObjectStatus,
  Form,
  Text,
  Panel,
} from "@ui5/webcomponents-react";
import {
  ObjectPageTitle,
  ObjectPageHeader,
  ObjectPageSection,
  ObjectPageSubSection,
} from "@ui5/webcomponents-react";
import { ToolbarButton } from "@ui5/webcomponents-react";
import { BreadcrumbsItem } from "@ui5/webcomponents-react";
import { FormGroup, FormItem } from "@ui5/webcomponents-react";

function App() {
  function redirectLinkedin() {
    window.open(
      "https://www.linkedin.com/in/lucas-yo-sato/",
      "_blank",
      "noopener,noreferrer"
    );
  }

  function redirectVaVi() {
    window.open(
      "https://www.linkedin.com/in/lucas-yo-sato/details/projects/",
      "_blank",
      "noopener,noreferrer"
    );
  }

  function onDownloadCV() {
    const pdfUrl = "CV.pdf";
    const link = document.createElement("a");
    link.href = pdfUrl;
    link.download = "CV.pdf"; // specify the filename
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  }

  return (
    <ObjectPage
      headerArea={
        <ObjectPageHeader>
          <FlexBox alignItems="Center" wrap="Wrap" style={{ padding: "15px" }}>
            <FlexBox direction="Column">
              <Link
                href="https://www.linkedin.com/in/lucas-yo-sato/"
                target="_blank"
                rel="noopener noreferrer"
              >
                LinkedIn
              </Link>
              <Link
                href="mailto:syoheidev@gmail.com"
                target="_blank"
                rel="noopener noreferrer"
              >
                syoheidev@gmail.com
              </Link>
              <Link
                href="https://github.com/satoajato"
                target="_blank"
                rel="noopener noreferrer"
              >
                https://github.com/satoajato
              </Link>
            </FlexBox>
            <FlexBox direction="Column" style={{ paddingLeft: "50px" }}>
              <Label>Arapongas</Label>
              <Label>PR, Brazil</Label>
            </FlexBox>
          </FlexBox>
        </ObjectPageHeader>
      }
      image={imageToAdd}
      imageShapeCircle
      mode="Default"
      onBeforeNavigate={function ks() {}}
      onPinButtonToggle={function ks() {}}
      onSelectedSectionChange={function ks() {}}
      onToggleHeaderArea={function ks() {}}
      selectedSectionId="goals"
      titleArea={
        <ObjectPageTitle
          breadcrumbs={
            <Breadcrumbs>
              <BreadcrumbsItem>satoajato.github.io</BreadcrumbsItem>
              <BreadcrumbsItem>Meu Portflio</BreadcrumbsItem>
            </Breadcrumbs>
          }
          actionsBar={
            <Toolbar design="Transparent">
              <ToolbarButton
                onClick={onDownloadCV}
                endIcon="download"
                design="Emphasized"
                text="Baixar CV"
              />
            </Toolbar>
          }
          header="Lucas Y. O. Sato"
          subHeader="Consultor SAP"
        ></ObjectPageTitle>
      }
    >
      <ObjectPageSection aria-label="Goals" id="goals" titleText="Objetivos">
        <Form>
          <FormItem
            labelContent={<Label showColon>Estudar Fiori, CAP e RAP</Label>}
          >
            <Text>Em andamento</Text>
          </FormItem>
          <FormItem
            labelContent={
              <Label showColon>Comear MBA em Engenharia de Software</Label>
            }
          >
            <Text>Pendente 03/2025</Text>
          </FormItem>
        </Form>
      </ObjectPageSection>
      <ObjectPageSection aria-label="Sobre" id="about" titleText="Sobre">
        <ObjectPageSubSection
          actions={
            <Button
            icon="action"
              design="Emphasized"
              onClick={redirectLinkedin}
              style={{ minWidth: "120px" }}
            >
              Acessar perfil no LinkedIn
            </Button>
          }
          aria-label="Informaes"
          id="infos"
          titleText="Informaes"
        >
          <Panel style={{ alignItems: "center" }}>
            <Text>
              Ol, seja bem vindo ao meu portflio. Eu sou bacharel em Cincia
              da Computao e desenvolvedor SAP ABAP e Fiori.
            </Text>
          </Panel>
          <Form
            style={{
              alignItems: "baseline",
            }}
          >
            <FormGroup headerText="Formao">
              <FlexBox direction="Column">
                <FormItem
                  labelContent={<Label showColon>Ensino Superior</Label>}
                >
                  <Text>
                    Bacharel em Cincia da Computao pela Universidade Estadual
                    do Paran
                  </Text>
                </FormItem>
              </FlexBox>
            </FormGroup>
            <FormGroup headerText="Minhas redes sociais">
              <FlexBox direction="Column">
                <FormItem labelContent={<Label showColon>LinkedIn</Label>}>
                  <Link
                    href="https://www.linkedin.com/in/lucas-yo-sato/"
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    /lucas-yo-sato
                  </Link>
                </FormItem>
                <FormItem labelContent={<Label showColon>Instagram</Label>}>
                  <Link
                    href="https://www.instagram.com/yoheilsato/"
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    @YoheilSato
                  </Link>
                </FormItem>
              </FlexBox>
            </FormGroup>
            <FormGroup headerText="Email">
              <FormItem labelContent={<Label showColon>Pessoal</Label>}>
                <Link
                  href="mailto:syoheidev@gmail.com"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  syoheidev@gmail.com
                </Link>
              </FormItem>
            </FormGroup>
          </Form>
        </ObjectPageSubSection>
        <ObjectPageSubSection
          aria-label="Experincia"
          id="exp"
          titleText="Experincia"
        >
          <Form>
            <FormGroup headerText="Tata Consultancy Services - Desde Out. 2023">
              <FormItem
                labelContent={<Label showColon>TranS4mar - Petrobras</Label>}
              >
                <FlexBox direction="Column">
                  <Text>Torre de Sustentao: AMS</Text>
                  <Text>
                    Torre de Projeto: Melhorias em aplicaes do mdulo de
                    Finanas
                  </Text>
                </FlexBox>
              </FormItem>
            </FormGroup>
          </Form>
        </ObjectPageSubSection>
        <ObjectPageSubSection
          aria-label="Skills"
          id="skills"
          titleText="Skills"
        >
          <Form>
            <FormGroup headerText="Desenvolvimento Ambiente SAP">
              <FormItem>
                <FlexBox alignItems="Stretch" direction="Column">
                  <Text>- ABAP</Text>
                  <Text>- CDS View</Text>
                  <Text>- Fiori</Text>
                </FlexBox>
              </FormItem>
            </FormGroup>
            <FormGroup headerText="Desenvolvimento Web e Mobile">
              <FormItem>
                <FlexBox alignItems="Stretch" direction="Column">
                  <Text>- React</Text>
                  <Text>- Flutter</Text>
                </FlexBox>
              </FormItem>
            </FormGroup>
          </Form>
        </ObjectPageSubSection>
      </ObjectPageSection>
      <ObjectPageSection
        aria-label="Projetos"
        id="projects"
        titleText="Projetos"
      >
        <ObjectPageSubSection
          actions={
            <Button
              icon="action"
              design="Emphasized"
              onClick={redirectVaVi}
              style={{ minWidth: "120px" }}
            >
              Acessar projetos no LinkedIn
            </Button>
          }
          aria-label="Em desenvolvimento"
          id="dev"
          titleText="Em desenvolvimento"
        >
        <Form>
          <FormGroup>
            <FormItem labelContent={<Label showColon>Aplicativo VaVi</Label>}>
              <Text>
                Aplicao mvel para dispositivos Android para oferecer suporte
                e informaes essenciais em momentos crticos.
              </Text>
            </FormItem>
          </FormGroup>
        </Form>
        </ObjectPageSubSection>
      </ObjectPageSection>
      <ObjectPageSection aria-label="Mentores" id="coachs" titleText="Mentores">
        <Form>
          <FormGroup headerText="SAP ABAP">
            <FlexBox direction="Column">
              <FormItem>
                <Avatar shape="Circle">
                  <img src={geovaneImg} alt="Foto de Geovane Alborghetti" />
                </Avatar>
              </FormItem>
              <FormItem
                labelContent={<Label showColon>Geovane Alborghetti</Label>}
              >
                <Text>Me ensinou ABAP Clssico, ABAP Moderno e Debug</Text>
              </FormItem>
            </FlexBox>
          </FormGroup>

          <FormGroup headerText="Fiori, UI5">
            <FlexBox direction="Column">
              <FormItem>
                <Avatar shape="Circle">
                  <img src={diegoImg} alt="Foto de Diego Ferrareze" />
                </Avatar>
              </FormItem>
              <FormItem labelContent={<Label showColon>Diego Ferrareze</Label>}>
                <Text>Me ensinou Fiori Freestyle</Text>
              </FormItem>
            </FlexBox>
          </FormGroup>
        </Form>
      </ObjectPageSection>
    </ObjectPage>
  );
}

export default App;
